.notFound {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80vh;
}

.notFoundDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  &__number {
    position: relative;
    letter-spacing: 5vmin;
    text-shadow:
      2px -1px 0 #000,
      4px -2px 0 #0a0a0a,
      6px -3px 0 #0f0f0f,
      8px -4px 0 #141414,
      10px -5px 0 #1a1a1a,
      12px -6px 0 #1f1f1f,
      14px -7px 0 #242424,
      16px -8px 0 #292929;
    background: #fff;
  }

  &__number::before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    background-color: #673ab7;
    background-image:
      radial-gradient(
        closest-side at 50% 50%,
        #ffc107 100%,
        rgb(0 0 0 / 0%)
      ),
      radial-gradient(closest-side at 50% 50%, #e91e63 100%, rgb(0 0 0 / 0%));
    background-position: -100vmin 20vmin, 100vmin -25vmin;
    background-size: 40vmin 40vmin;
    background-repeat: repeat-x;
    animation: moving 10s linear infinite both;
    mix-blend-mode: screen;
  }

  &__text {
    display: flex;
    flex-direction: column;
    align-self: center;
    margin-bottom: 2rem;
  }

  &__text span {
    font-size: 3rem;
  }
}

@keyframes moving {
  to {
    background-position: 100vmin 20vmin, -100vmin -25vmin;
  }
}

@keyframes moving {
  to {
    background-position: 100vmin 20vmin, -100vmin -25vmin;
  }
}
