@import "/src/styles/variables";
@import "/src/styles/global";

.comment-panel {
  &__inner {
    @include media(mobile) {
      height: 5em;
    }

    display: flex;
    flex-direction: column;
    gap: 1em;
    height: 100%;
  }

  &__control {
    display: flex;
    gap: 1em;
    margin-top: 1em;
  }

  &__content {
    flex-grow: 1;
    width: 100%;
    padding: 1em;
    font-size: 1.3em;
  }
}
